.sidebar {
  position: fixed;
  top: 0;
  left: 0;
  width: 15%;
  height: 100vh;
  z-index: 1;
  background-color: #222f3e;
}

.sidebar_toggel {
  position: fixed;
  top: 0;
  left: 0;
  width: 5%;
  height: 100vh;
  z-index: 1;
  background-color: #222f3e;
}

.img_section img {
  width: inherit;
  height: inherit;

}

.img_section {
  height: 10.375rem;
  width: 9.375rem;
  overflow: hidden;
  border-radius: 50%;
  margin-right: 1.5625rem;
  border: .3125rem solid white;
}

.img_section_toggel {
  display: none;
}
.sidebar_top {
  display: flex;
  margin-top: 1.5625rem;
  justify-content: right;
  align-items: start;
}

.logo {
  font-size: 1.5625rem;
  background-color: yellow;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: .25rem .25rem;
}

.nav_bar {
  display: flex;
  flex-direction: column;
  padding-left: 2.5rem;
  gap: 2.5rem 0px;
  margin-top: 3.125rem;
  font-size: 1.125rem;
  color: white;
  transition: 300ms;
  user-select: none;
}

.nav_bar_toggel {
  display: flex;
  flex-direction: column;
  padding-left: 1.25rem;
  gap: 2.5rem 0px;
  margin-top: 3.125rem;
  font-size: 1.125rem;
  color: white;
  transition: 200ms;
}

.nav_item svg {
  margin-right: .9375rem;
  font-size: 1.5625rem;
}

.nav_item {
  transition: all 200ms;
}

.nav_item:hover {
  color: deepskyblue;
  cursor: pointer;
}

.nav_item .active {
  text-decoration: none;
  color: yellow;
}

