.tackstack h2 {
  color: teal;
}

.tackstack p{
  font-family: serif;
  text-transform: capitalize;
  font-size: 1.25rem;
}

.tech_icon {
  margin-top: -10px;
  margin-right: 10px;
  font-size: 30px;
}

.card_section {
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  cursor: pointer;
  transition: 500ms;
}

.card_section a{
  text-decoration: none;
  color:#0a3d62 ;
  transition: 100ms;
}
.card_section:hover a{
  color: white;
  
}
.card_section:hover {
  background-color: darkslategray;
  color: white;
  transform: translateY(-10px);
}

