.main {
  background-color: black;
  padding-left: 15%;
  transition: 500ms;
}
.main_section {
  overflow: hidden;
  width: 100%;
}

.mainp {
  padding-left: 15%;
  transition: 500ms;
}

.mainT {
  padding-left: 5%;
  transition: 500ms;
}

.footer {
  background-color: rgb(11, 12, 13);
  height: 6.25rem;
  padding: 0%;
  color: white;
  text-align: center;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Media query  */
@media (max-width: 600px) {
  .mainp,
  .mainT {
    padding: 0%;
  }
  .main {
    background-color: black;
    padding-left: 6%;
    transition: 500ms;
  }
}

/* media query small device */

@media (min-width: 600px) {
  .mainp {
    padding-left: 35%;
    transition: 500ms;
  }

  .mainT {
    padding-left: 11%;
    transition: 500ms;
  }
}

@media (min-width: 768px) {
  .mainp {
    padding-left: 27%;
    transition: 500ms;
  }

  .mainT {
    padding-left: 11%;
    transition: 500ms;
  }
}

@media (min-width: 992px) {
  .mainp {
    padding-left: 25%;
    transition: 500ms;
  }

  .mainT {
    padding-left: 8%;
    transition: 500ms;
  }
}

@media (min-width: 1200px) {
  .mainp {
    padding-left: 20%;
    transition: 500ms;
  }

  .mainT {
    padding-left: 5%;
    transition: 500ms;
  }
}

@media (min-width: 1400px) {
  .mainp {
    padding-left: 15%;
    transition: 500ms;
  }

  .mainT {
    padding-left: 5%;
    transition: 500ms;
  }
}
