.heading {
  color: teal;
}

.project p {
  text-align: justify;
}

.project .card {
  padding: 10px;
  transition: all 500ms;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  cursor: pointer;
  transform: scale(0.9);
}

.project .card:hover {
  background-color: #2c3a47;
  color: white;
  transform: scale(1);
}

.project .card:hover a{
  color: white;
  transition: 200ms;
}

.card_details_badge {
  padding: 4px 5px;
}

.ad-btn {
  background-color: #1b9cfc;
  color: white;
  padding: 3px 10px;
  text-transform: uppercase;
  text-decoration: none;
  transition: all 500ms;
  margin-top: 10px;
  border-radius: 5px;
}

.ad-btn:hover {
  color: black;
  background-color: white;
}

.resetProject {
  font-family: serif;
  text-transform: capitalize;
  text-align: justify;
  font-size: 1.25rem;
}

/* .projects div {
  background-color: #1b9cfc;
} */
.projects {
  width: 100%;
  display: grid;
  grid-template-columns: 23% 23% 23% 23%;
  column-gap: 2%;
  transition: 200ms;
}

.projects .card img {
 
  width: 100%;
  margin: 0px auto;
  
}

.projects div {
  transition: 200ms;
}
@media (min-width: 0px) {
  .projects {
    width: 100%;
    display: grid;
    grid-template-columns: 100%;
    row-gap: 1.25rem;
  }
  .card-body h4{
    font-size: 1rem;
  }
}

@media (min-width: 480px) {
  .projects {
    width: 100%;
    display: grid;
    grid-template-columns: 48% 48%;
    column-gap: 2%;
    row-gap: 1.25rem;
    transition: 200ms;
  }
  .card-body h4{
    font-size: 1rem;
  }
}

@media (min-width: 769px) {
  .projects {
    width: 100%;
    display: grid;
    grid-template-columns: 32% 32% 32%;
    column-gap: 3%;
    row-gap: 1.25rem;
  }
}

@media (min-width: 1279px) {
  .projects {
    width: 100%;
    display: grid;
    grid-template-columns: 23% 23% 23% 23%;
    column-gap: 2%;
  }
  .card-body h4{
    font-size: 1.25rem;
  }
}
