.topbar {
  padding-left: 40px;
  padding-top: 50px;
  color: white;
  padding-bottom: 4.125rem;
}

.top_head {
  color: #fffa65;
}

.home_button .btn-hire {
  background-color: dodgerblue;
  color: white;
  width: 10rem;
  font-size: medium;
  transition: 1s;
}

.home_button .btn-cv {
  background-color: #f79f1f;
  color: white;
  margin-left: 20px;
  transition: 500ms;
  border: 3px solid transparent;
}

.home_button .btn-cv:hover {
  background-color: lawngreen;
  color: black;
  margin-left: 20px;
  border: 3px solid white;
}

.them_btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  margin-top: 20px;
  position: fixed;
  cursor: pointer;
  right: 10px;
  background-color: darkslategray;
  z-index: 1;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}

@media (max-width: 400px) {
  .topbar h2 {
    font-size: 1.25rem;
    margin-top: 1.875rem;
    text-align: center;
  }
  .topbar {
    padding-left: 0%;
    padding-bottom: 0%;
  }
  .topbar h1 {
    font-size: 1.25rem;
    text-align: center;
  }

  .home_button .btn-hire {
    width: 6.25rem;
    font-size: 1rem;
    margin-bottom: 1.25rem;
  }

  .home_button .btn-cv {
    margin-bottom: 1.25rem;
    font-size: small;
  }

  .home_button {
    margin: 0px auto;
    display: block;
    text-align: center;
  }

  .home_button .btn-hire:active {
    background-color: white;
    color: dodgerblue;
    width: 9.375rem;
  }
}

@media (max-width: 600px) {
  .topbar h2 {
    font-size: 1.25rem;
    margin-top: 1.875rem;
    text-align: center;
  }
  .topbar {
    padding-left: 0%;

  }
  .topbar h1 {
    font-size: 1.25rem;
    text-align: center;
  }

  .home_button .btn-hire {
    width: 6.25rem;
    font-size: 1rem;
    margin-bottom: 1.25rem;
  }

  .home_button .btn-cv {
    margin-bottom: 1.25rem;
    font-size: small;
  }

  .home_button {
    margin: 0px auto;
    display: block;
    text-align: center;
  }

  .home_button .btn-hire:active {
    background-color: white;
    color: dodgerblue;
    width: 9.375rem;
  }
}

.heading_title {
  margin-left: 0px;
  transition: all 500ms;
}

@media (min-width: 600px) {
  .heading_title {
    margin-left: 9.375rem;
  }
  .home_button .btn-hire:hover {
    background-color: white;
    color: dodgerblue;
    width: 12.5rem;
  }
}

