.mobail_nav {
  overflow: hidden;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1;
  height: auto;
}

.mobail-nav_header {
  width: 100%;
  height: 50px;
  background-color: #1e1e1e;
  padding: 8px;
  
}

.mobail-nav_header .mobail_nav_icon {
  color: white;
  cursor: pointer;
  font-size: 1.5625rem;
  margin-left: .625rem;
}
.mobail_nav_title {
  color:whitesmoke;
  margin-left: 1.25rem;
  font-weight: bold;
  text-transform: capitalize;
  vertical-align: middle;
}
.mobail_nav_menu {
  width: 15.625rem;
  background-color: #1e1e1e;
  color: white;
  padding: 1.25rem;
  border-bottom-right-radius: 1.25rem;
  transition: all 200ms;
  height: 20rem;
}
.mobail_nav_menu_toggel {
  width: 15.625rem;
  background-color: #1e1e1e;
  color: white;

  border-bottom-right-radius: 1.25rem;
  transition: all 200ms;
  height: 0rem;
}
.mobail_nav_menu .nav_item {
  margin-bottom: 1rem;
  font-size: 1rem;
}

.mobail_nav_menu .nav_item:hover {
  color: darkorange;
}

.mobail_nav_menu_toggel .nav_item {
  margin-bottom: 1rem;
  font-size: 1rem;
}
.mobail_nav_menu .nav_item svg {
  font-size: 1.25rem;
}
@media (min-width: 600px) {
  .mobail_nav {
    display: none;
  }
}
